<template>
  <div class="home">
    <img alt="Vue logo" src="../assets/bike.png" />
    <h1>Welcome to City Bike Rentals!</h1>
    <BikeStationButtons />
    <div class="search-session">
    <input class="search-input" v-model="sessionId" placeholder="Enter Session Id">
    <button class="search-btn" @click="searchSessionById(sessionId)">Search</button>
    </div>
  </div>
</template>

<script setup>
    import BikeStationButtons from "@/components/BikeStationButtons.vue";
    import router from '@/router';
    import {globalGetConnect} from '@/global.js';

    const sessionId = ""

    async function searchSessionById(sessionId){
      const baseUrl = "api/rentals/getsession/" + sessionId
      const response = (await globalGetConnect(baseUrl))

      const dateObj = new Date(response.data[0].start_time);

      router.push({
          path: '/session/' + sessionId + "/" + dateObj.toLocaleTimeString() + "/" + response.data[0].session_length + "/" + response.data[0].bike_id
      })
    }
</script>

<style scoped>
.search-btn {
  border-radius: 10px;
  background-color: #a0c8d7;
  cursor: pointer;
  margin-top: 30px;
  margin-left: 5px;
  height: inherit;
}

.search-input {
  border: 2px solid black;
  border-radius: 10px;
  height: inherit;
  width: 300px;
  padding-left: 10px;
}

.search-session {
  height: 35px;
}
</style>
