<template>
  <div class="station-row">
  <button v-for="(station, index) in stations" :key="station.id" class="station-btn" @click="openStation(station.name)"><h4>{{ station.name }} Station</h4><p>( {{ numOfBikesInStations[index] }} / {{ station.total_stalls }})</p></button>
  </div>
</template>

<script setup>
    import router from '@/router';
    import { ref, onMounted } from "vue";
    import {globalGetConnect} from '@/global.js';

    const stations = ref([]);
    const numOfBikesInStations = ref([]);

    async function getAllStations() {
       const baseUrl = "api/locations/stations"
      stations.value = (await globalGetConnect(baseUrl)).data
    }

    async function getNumberofBikesInEachStation() {
      for(const station of stations.value) {
          const baseUrl = "api/locations/bikes/" + station.name + "/count"
          const response = (await globalGetConnect(baseUrl))
          numOfBikesInStations.value.push(response.data[0].string);
      }
    }

    onMounted(async () => {
      await getAllStations();
      await getNumberofBikesInEachStation();
    });

    function openStation(stationName) {
      router.push({
        path: '/station/' + stationName
      })
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
p {
  font-size: medium;
}
h4 {
  margin-top: 10px;
}
.station-row {
  margin-top: 30px;
}
.station-btn {
  width: 250px;
  border-radius: 10px;
  background-color: #a0c8d7;
  margin: 0px 20px;
  cursor: pointer;
}

</style>
