import axios from 'axios'

const originUrlOne = "http://localhost:8080/"
const originUrlTwo = "http://localhost:8090/"

export const globalGetConnect = async (baseUrl) => {
    try {
        const response = await axios.get(
        originUrlOne + baseUrl
        );

        console.log(JSON.stringify(response.data))

        return response
    } catch (error) {
        console.log("here")
        try {
        const response = await axios.get(
            originUrlTwo + baseUrl
        );
        
        return response
        } catch(error2) {
        console.log(error2 + " " + error);
        }
    }
}

export const globalPostConnect = async (baseUrl) => {
    try {
        const response = await axios.post(
        originUrlOne + baseUrl
        );

        console.log(JSON.stringify(response.data))

        return response
    } catch (error) {
        console.log("here")
        try {
        const response = await axios.post(
            originUrlTwo + baseUrl
        );
        
        return response
        } catch(error2) {
        console.log(error2 + " " + error);
        }
    }
}